@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: "Roboto", sans-serif;
    scroll-behavior: smooth;
}
#footer__wrapper {
    color: white;
    background-color: #242020;
    width: 100%;
}

.footer {
    color: white;
    background-color: #242020;
    width: 1440px;
    max-width: 90%;
    margin: 0 auto;
    padding: 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.contacts h2 {
    padding-bottom: 1rem;
    font-weight: 500;
}

.contacts p {
    font-size: 0.8em;
    display: flex;
    align-items: center;
}

.contacts p span {
    padding: 0.2rem 1rem 0.2rem 0;
}

.greetings {
    font-weight: 300;
}


/*# sourceMappingURL=Footer.css.map */

@media (max-width: 945px) {
    .footer {
        flex-direction: column;
    }
    .contacts {
        padding-bottom: 2rem;
        text-align: center;
    }
    .contacts p {
        justify-content: center;
        font-size: 1.2rem;
        align-items: center;
    }
    .contacts h2 {
        font-size: 2rem;
    }
    .greetings {
        padding-top: 2rem;
        text-align: center;
    }
}
.hero {
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.hero h1,
.hero h2,
.hero button {
    text-align: center;
    max-width: 90%;
    position: absolute;
    color: white;
}

.hero button {
    font-size: x-large;
    font-weight: 500;
    background-color: transparent;
    padding: 0.8rem 2rem;
    outline: none;
    border: 1px solid white;
    cursor: pointer;
    transition: all 0.5s ease;
}

.hero button:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    cursor: pointer;
    background-color: white;
    color: black;
}

.hero h2 {
    top: 47%;
    font-weight: 500;
}

.hero button {
    top: 55%;
}


/*# sourceMappingURL=Hero.css.map */

@media (max-width: 815px) {
    .hero h2 {
        top: 55%;
    }
    .hero button {
        top: 62%;
    }
}
.imagesGrid__wrapper {
    width: 100%;
    height: auto;
    background-color: whitesmoke;
    padding-bottom: 3rem;
}

.image__gallery {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (1fr) [4];
    grid-template-columns: repeat(4, 1fr);
    -ms-grid-rows: auto;
    grid-template-rows: auto;
    grid-template-areas: "img-7 img-2 img-3 img-3" "img-7 img-1 img-3 img-3";
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.image__gallery img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
}

.img1 {
    -ms-grid-row: 2;
    -ms-grid-column: 2;
    grid-area: img-1;
}

.img2 {
    -ms-grid-row: 1;
    -ms-grid-column: 2;
    grid-area: img-2;
}

.img3 {
    -ms-grid-row: 1;
    -ms-grid-row-span: 2;
    -ms-grid-column: 3;
    -ms-grid-column-span: 2;
    grid-area: img-3;
}

.img4 {
    grid-area: img-4;
}

.img5 {
    grid-area: img-5;
}

.img6 {
    grid-area: img-6;
}

.img7 {
    -ms-grid-row: 1;
    -ms-grid-row-span: 2;
    -ms-grid-column: 1;
    grid-area: img-7;
}

.img8 {
    grid-area: img-8;
}


/*# sourceMappingURL=ImagesGrid.css.map */

@media (max-width: 840px) {
    .image__gallery {
        grid-gap: 0.5rem;
        gap: 0.5rem;
    }
}
.jedlo-wrapper {
    height: auto;
    width: 100%;
    background-color: whitesmoke;
    display: flex;
    justify-content: center;
}

.jedlo {
    width: 1440px;
    max-width: 90%;
    padding: 2rem;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.jedlo .svadobnemenu__bufet {
    margin-top: 5rem;
}

.jedlo .svadobnemenu,
.jedlo .svadobnemenu__bufet {
    text-align: center;
    width: 100%;
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.jedlo .svadobnejedla,
.jedlo .svadobnejedla__bufet {
    padding-top: 2rem;
    display: flex;
    justify-content: space-around;
    width: 70%;
}

.jedlo .svadobnejedla img,
.jedlo .svadobnejedla__bufet img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.5s ease;
}

.jedlo .svadobnejedla img:hover,
.jedlo .svadobnejedla__bufet img:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.jedlo .svadobnejedla div,
.jedlo .svadobnejedla__bufet div {
    width: 10rem;
    height: 10rem;
    background-color: blue;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
}

.jedlo .svadobnejedla__text {
    font-weight: 300;
    padding-top: 1rem;
    display: flex;
    justify-content: space-around;
    width: 70%;
}

.jedlo .jedlo__footer {
    box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
    background-color: white;
    padding: 2rem 0;
    margin-top: 4rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.jedlo .jedlo__footer .jedlo__img {
    margin-right: 3rem;
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    overflow: hidden;
}

.jedlo .jedlo__footer .jedlo__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.5s ease;
}

.jedlo .jedlo__footer .jedlo__img img:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.jedlo__text h2 {
    color: #3f3f3f;
    font-weight: 400;
    padding-bottom: 1rem;
}

.jedlo__text h3 {
    font-weight: 500;
}

.jedlo__text p {
    font-style: italic;
}


/*# sourceMappingURL=Jedlo.css.map */

@media (max-width: 1140px) {
    .jedlo .svadobnejedla div,
    .jedlo .svadobnejedla__bufet div {
        width: 7rem;
        height: 7rem;
    }
}

@media (max-width: 825px) {
    .jedlo .svadobnejedla,
    .jedlo .svadobnejedla__bufet {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        justify-items: center;
    }
    .jedlo .svadobnejedla div,
    .jedlo .svadobnejedla__bufet div {
        width: 10rem;
        height: 10rem;
        margin-bottom: 1rem;
    }
}

@media (max-width: 650px) {
    .jedlo .svadobnejedla,
    .jedlo .svadobnejedla__bufet {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .jedlo .svadobnemenu__bufet {
        margin-top: 1rem;
    }
    .jedlo .svadobnejedla div,
    .jedlo .svadobnejedla__bufet div {
        width: 20rem;
        height: 10rem;
        border-radius: 0;
        margin-bottom: 1rem;
    }
    .jedlo .jedlo__footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 1rem 1rem;
    }
    .jedlo .jedlo__footer .jedlo__img {
        margin: 0px;
    }
    .jedlo__text {
        text-align: center;
    }
    .jedlo__text h2 {
        padding-top: 2rem;
    }
    .jedlo .svadobnejedla__text {
        display: flex;
        flex-direction: column;
    }
    .jedlo .svadobnejedla__text p {
        font-size: 1.5rem;
        padding-bottom: 1rem;
    }
}
.navbar {
    width: 100%;
    height: 3rem;
    position: fixed;
    z-index: 1;
    top: 0;
}

.navbar__active {
    background-color: white;
    transition: all 1s ease;
}

.nav {
    width: 1440px;
    max-width: 90%;
    height: 100%;
    margin: 0 auto;
}

.nav ul {
    height: 100%;
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav ul li {
    padding: 0 2rem;
}

.nav ul li a {
    color: white;
    text-decoration: none;
}

.nav ul li a:hover {
    font-weight: 500;
    border-bottom: 1px solid whitesmoke;
}

.nav__active ul li a {
    color: black;
}

.nav__active ul li a:hover {
    border-bottom: 1px solid black;
}

.menuIcon {
    color: #222222;
    position: absolute;
    font-size: 2rem !important;
    top: 10px;
    right: 15px;
    display: none !important;
}

.nav__logo {
    height: 100%;
}

.nav__logo img {
    width: 5rem;
    position: relative;
    transition: all 1s ease;
}

.nav__logo .logo__show {
    width: 3rem;
}


/*# sourceMappingURL=Navbar.css.map */

@media (max-width: 768px) {
    .menuIcon {
        display: inline !important;
    }
    .navbar_hamburger__active {
        height: 50%;
        background-color: rgba(214, 62, 85, 0.8);
        transition: all 0.5s ease;
    }
    .nav {
        height: 100%;
        display: none;
    }
    .nav_hamburger__active {
        display: inline;
    }
    .nav ul {
        display: flex;
        flex-direction: column;
    }
    .nav ul li {
        font-size: 1.5rem;
        text-align: center;
        padding: 1rem 2rem;
        width: 100%;
    }
}
.planpobytu__wrapper {
    padding-top: 1rem;
    background-color: whitesmoke;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.planpobytu__wrapper h1 {
    padding-bottom: 2rem;
    font-size: xx-large;
}

.plan__pobytu {
    width: 1440px;
    max-width: 90%;
    display: -ms-grid;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.plan__pobytu h1 {
    padding-bottom: 3rem;
}


/*# sourceMappingURL=PlanPobytu.css.map */

@media (max-width: 650px) {
    .planpobytu__wrapper {
        height: auto;
    }
    .plan__pobytu {
        display: flex;
        flex-direction: column;
    }
    .planpobytu__wrapper h1 {
        padding: 3rem 0;
    }
}
.PPcard {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
}

.PPcard .planDay {
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    overflow: hidden;
    height: 15rem;
    width: 100%;
    background-color: black;
    text-align: center;
}

.PPcard .planDay img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.9;
    transition: all 0.5s ease;
}

.PPcard .planDay img:hover {
    opacity: 1;
    -webkit-transform: scale(1.03);
    transform: scale(1.03);
}

.PPcard .plan {
    padding: 2rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.PPcard .plan h2 {
    padding-bottom: 1rem;
    font-size: 1.4rem;
}

.PPcard .plan p {
    font-size: 0.8rem;
}

.PPcard .plan h3 {
    font-size: 1rem;
}

.PPcard .plan .sectionOne {
    padding-bottom: 1rem;
}

.PPcard:hover {
    background-color: #f9f9f9;
}


/*# sourceMappingURL=PlanPobytuCard.css.map */

@media (max-width: 650px) {
    .PPcard .plan {
        align-items: center;
    }
    .sectionTwo,
    .sectionOne {
        text-align: center;
    }
    .PPcard .plan h2 {
        font-size: 2rem;
    }
    .PPcard .plan p {
        font-size: 1.2rem;
    }
    .PPcard .plan h3 {
        font-size: 1.3rem;
    }
}
.ponuka {
    height: auto;
    padding: 5rem 0;
    width: 100%;
    background-color: white;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}

.ponuka img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ponuka div {
    height: 250px;
}

.ponuka__prenevestu,
.ponuka__prezenicha,
.ponuka__prostredie,
.ponuka__ubytovanie {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5rem;
}

.ponuka__prenevestu::before,
.ponuka__prezenicha::before,
.ponuka__prostredie::before,
.ponuka__ubytovanie::before {
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-color: rgba(0, 0, 0, 0.5);
}

.ponuka h2 {
    position: relative;
    color: white;
    opacity: 1;
    padding-bottom: 1rem;
}

.ponuka p {
    position: relative;
    color: white;
    font-weight: 300;
    text-align: center;
}


/*# sourceMappingURL=Ponuka.css.map */

@media (max-width: 650px) {
    .ponuka {
        display: flex;
        flex-direction: column;
    }
    .ponuka h2 {
        font-size: 1.8rem;
    }
    .ponuka p {
        font-size: 1.12rem;
        text-align: center;
    }
}
